import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { DriverVechileMappingModel } from '../models/model';

@Injectable({
  providedIn: 'root'
})
export class DriverVechileMappingsService {
  apiUrl = `${environment.apiUrl}/api/UserVechileMappings`;

  constructor(private http: HttpClient) { }

  get() {
    return this.http.get(this.apiUrl);
  }

  getById(id: number) {
    return this.http.get(`${this.apiUrl}/${id}`);
  }

  create(driverVechileMapping: DriverVechileMappingModel) {
    return this.http.post(this.apiUrl, driverVechileMapping);
  }
  update(driverVechileMapping: DriverVechileMappingModel) {
    return this.http.put(`${this.apiUrl}/${driverVechileMapping.id}`, driverVechileMapping);
}

  delete(id: number) {
    return this.http.delete(`${this.apiUrl}/${id}`);
  }

  updateLocation(id: number, latitude: number, longitude: number) {
    return this.http.put(`${this.apiUrl}/${id}/${latitude}/${longitude}`, {});
  }
}
