import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { forkJoin } from 'rxjs';
import { DriverVechileMappingModel } from 'src/app/shared/models/model';
import { DriverVechileMappingsService } from 'src/app/shared/services/driver-vechile-mappings.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';
import { UserService } from 'src/app/shared/services/user.service';
import { VechileRouteService } from 'src/app/shared/services/vechile-route.service';
import { VehicleService } from 'src/app/shared/services/vehicle.service';

@Component({
  selector: 'app-driver-vehicle-mapping',
  templateUrl: './driver-vehicle-mapping.component.html'
})
export class DriverVehicleMappingComponent implements OnInit {
  driverVehicleForm!: FormGroup;

  drivers: any[] = [];
  vehicles: any[] = [];
  vechileRoutes: any[] = [];
  driverVehicles: any[] = [];
  display: boolean = false;

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private vehicleService: VehicleService,
    private vechileRouteService: VechileRouteService,
    private driverVechileMappingsService: DriverVechileMappingsService,
    private toastrService: ToastrService) {
    this.driverVehicleForm = this.fb.group({
      id: [''],
      vechileId: ['', Validators.required],
      routeId: ['', Validators.required],
      latitude: [''],
      logitude: [''],
      userId: ['', Validators.required],
      isActive: ['true', Validators.required],
    });
  }

  ngOnInit() {
    this.loadData();
  }

  loadData() {
    forkJoin([
      this.userService.getDriverList(this.userService.selectedInstitutionId),
      this.vehicleService.get(this.userService.selectedInstitutionId),
      this.vechileRouteService.get(),
      this.driverVechileMappingsService.get()
    ]).subscribe(([drivers, vehicles, routes, driverVechiles]) => {
      this.drivers = drivers as any[];
      this.vehicles = vehicles as any[];
      this.vechileRoutes = routes as any[];
      this.driverVehicles = driverVechiles as any[];
    });
  }

  showDialog() {
    this.display = true;
    this.driverVehicleForm.reset();
  }

  updateData() {
    let temp = [...this.driverVehicles];
    temp = JSON.parse(JSON.stringify(temp));
    this.driverVehicles = temp;
  }

  add() {
    const data = this.driverVehicleForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.driverVechileMappingsService.create(data).subscribe({
      next: (response: any) => {
        if (!!response.id) {
          this.driverVehicles.push(response);
          this.updateData();
          this.toastrService.success('Driver Vechiles Create Successfully.');
          this.display = false;
        }
      },
      error: () => {
        this.toastrService.error('Driver Vechiles Create Failed.');
      }
    });
  }

  edit() {
    this.driverVechileMappingsService.update(this.driverVehicleForm.value).subscribe((response: any) => {
      const data = this.driverVehicleForm.value;
      const index = this.driverVehicles.findIndex(i => i.id === data.id);
      this.driverVehicles[index] = data;
      this.toastrService.success('Driver Vechiles Updated Successfully.');
      this.display = false;
    });
  }


  onSubmit() {
    if (this.driverVehicleForm.invalid) {
      return;
    }

    if (!this.driverVehicleForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  fillForm(drivervechile: DriverVechileMappingModel) {
    this.display = true;
    this.driverVehicleForm.patchValue(drivervechile);
  }

  delete(id: number) {
    this.driverVechileMappingsService.delete(id).subscribe((response: any) => {
      const index = this.driverVehicles.findIndex(i => i.id == id);
      this.driverVehicles.splice(index, 1);
      this.toastrService.error('Driver Vechiles Deleted Successfully.');
    });
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }

}
